import _get from 'lodash.get';
import Inputmask from 'inputmask';
import { ELEMENTS_TYPE, SURVEYJS } from '../../Constants';
import { CURRENCY_MASK, CURRENCY_TYPE } from './Constants';

export const TextInputFormat = Survey => {
	const widget = {
		name: 'textFormat',
		numericGroupSeparator: ',',
		numericAutoGroup: true,
		numericDigits: 2,
		numericDigitsOptional: false,
		numericPlaceholder: '0',
		autoUnmask: true,
		clearIncomplete: true,
		isDefaultRender: true,
		widgetIsLoaded() {
			return typeof Inputmask !== 'undefined';
		},
		isFit(question) {
			if (question.inputMask === CURRENCY_TYPE) return true;
			return [ELEMENTS_TYPE.text, ELEMENTS_TYPE.multipleText].includes(
				question.getType()
			);
		},
		activatedByChanged() {
			const properties = [
				{
					name: `prefix`,
					category: 'general',
				},
				{
					name: 'inputFormat',
					category: 'general',
				},
				{
					name: 'inputMask',
					category: 'general',
					visible: false,
				},
			];
			const metaData = _get(Survey, SURVEYJS.metaData);
			metaData.addProperties(ELEMENTS_TYPE.text, properties);
			metaData.addProperties(ELEMENTS_TYPE.matrixDropdownColumn, properties);
			metaData.addProperties(ELEMENTS_TYPE.multipleTextItem, properties);
		},
		applyCurrencyMask(question, el) {
			if (
				question.inputType !== CURRENCY_TYPE &&
				question.inputMask !== CURRENCY_TYPE
			)
				return;
			const options = {
				groupSeparator: this.numericGroupSeparator,
				autoGroup: this.numericAutoGroup,
				digits: this.numericDigits,
				digitsOptional: this.numericDigitsOptional,
				placeholder: this.numericPlaceholder,
				autoUnmask: true,
				prefix: question.prefix || '',
				clearIncomplete: true,
			};

			Inputmask(CURRENCY_MASK, options).mask(el);
		},
		applyInputFormat(question, el) {
			const { inputFormat } = question;

			if (!inputFormat) return;
			Inputmask(inputFormat).mask(el);
		},
		afterRender(question, rootElement) {
			if (question.getType() !== ELEMENTS_TYPE.multipleText) {
				const input = rootElement.querySelector('input') || rootElement;
				this.applyInputFormat(question, input);
				this.applyCurrencyMask(question, input);
				return;
			}

			const items = _get(question, 'items', []);
			items.forEach(item => {
				const input = rootElement.querySelector(
					`#${_get(item, 'editor.inputId', '')}`
				);
				if (input) {
					this.applyInputFormat(item, input);
					this.applyCurrencyMask(item, input);
				}
			});
		},
	};

	const Instance = _get(Survey, SURVEYJS.widgetCollectionInstance);
	Instance.addCustomWidget(widget);
};
