import produce from 'immer';
import * as uuid from 'uuid';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import _has from 'lodash.has';
import _invert from 'lodash.invert';
import {
	SURVEYJS_MODE,
	SURVEYJS_STATUS,
	FILE_UPLOADER_ACTION_ADD,
	FORM_TYPE,
} from '../views/application/components/FormBuilder/Constants';

export const isFormInDisplayMode = mode => mode === SURVEYJS_MODE.display;

export const isIterable = obj => Symbol.iterator in Object(obj);

export const exportDataByTemplateElements = templateElements => {
	if (templateElements && isIterable(templateElements)) {
		return templateElements.reduce((accumulator, item) => {
			const { name, questionId } = item;
			return {
				...accumulator,
				[name]: questionId,
			};
		}, {});
	}
	return {};
};

export const exportDataByElements = elements => {
	if (elements && isIterable(elements)) {
		return elements.reduce((accumulator, item) => {
			const questionName = _get(item, 'name');
			const questionId = _get(item, 'questionId');
			const templateElements = _get(item, 'templateElements');
			const elements = _get(item, 'elements');
			const nameAndQuestionIdInTemplate =
				exportDataByTemplateElements(templateElements);
			const nameAndQuestionIdInElement = exportDataByElements(elements);
			return {
				...accumulator,
				...nameAndQuestionIdInTemplate,
				...nameAndQuestionIdInElement,
				[questionName]: questionId,
			};
		}, {});
	}
	return {};
};

export const exportNameAndQuestionId = template => {
	const pages = _get(template, 'pages', []);
	if (pages && isIterable(pages)) {
		return pages.reduce((accumulator, page) => {
			const elements = _get(page, ['elements']);
			const data = exportDataByElements(elements);
			return {
				...accumulator,
				...data,
			};
		}, {});
	}
	return {};
};

export const deepMapKeys = (obj, fn) =>
	// eslint-disable-next-line no-nested-ternary
	Array.isArray(obj)
		? obj.map(val => deepMapKeys(val, fn))
		: typeof obj === 'object'
		? Object.keys(obj).reduce((acc, current) => {
				const key = fn(current);
				const val = obj[current];
				// eslint-disable-next-line no-param-reassign
				acc[key] =
					val !== null && typeof val === 'object' ? deepMapKeys(val, fn) : val;
				return acc;
		  }, {})
		: obj;

export const getFormMode = status =>
	status === SURVEYJS_STATUS.submitted
		? SURVEYJS_MODE.display
		: SURVEYJS_MODE.edit;

export const isFormSubmitted = formData => {
	if (!_has(formData, 'DataStatus')) return false;
	const status = _get(formData, 'DataStatus');
	return status === SURVEYJS_STATUS.submitted;
};

export const switchNameToId = (survey, template) => {
	let nameAndIdInData = {};
	let nameAndIdInTemplate = {};
	const data = _get(survey, ['data'], {});
	if (!_isEmpty(data)) {
		const questions = exportNameAndQuestionId(template);
		nameAndIdInTemplate = deepMapKeys(
			data,
			name =>
				_get(survey.getQuestionByName(name), 'questionId', '') ||
				_get(questions, name, name)
		);
	}
	if (_has(survey, ['questionHashes', 'names'])) {
		const questionsName = Object.keys(
			_get(survey, ['questionHashes', 'names'], {})
		);
		nameAndIdInData = questionsName.reduce((accumulator, name) => {
			const question = survey.getQuestionByName(name);
			const questionId = _get(question, 'questionId', '');
			const questionDefaultValue = _get(question, 'defaultValue') || '';
			const questionValue = survey.getValue(name) || questionDefaultValue;
			return {
				...accumulator,
				[questionId]: questionValue ?? '',
			};
		}, {});
	}
	return { ...nameAndIdInData, ...nameAndIdInTemplate };
};

export const applyFormProperty = (question, formProperty) => {
	if (_isEmpty(formProperty)) return;
	const questionId = _get(question, 'questionId');
	if (!formProperty[questionId]) return;
	const properties = Object.entries(formProperty[questionId]);
	properties.forEach(property => {
		const [propertyName, propertyValue] = property;
		try {
			if (propertyName === 'defaultValue') {
				// eslint-disable-next-line no-param-reassign
				question[propertyName] = propertyValue;
			} else {
				question.setPropertyValue(propertyName, propertyValue);
			}
		} catch (e) {
			// pass this propery
			// avoid improper properties
		}
	});
};

export const switchIdToName = (survey, data, template) => {
	const defaultData = _get(survey, 'data', {});
	let nameAndIdInTemplate = {};
	let nameAndIdInData = {};
	if (data && template) {
		const questions = _invert(exportNameAndQuestionId(template));
		nameAndIdInTemplate = deepMapKeys(data, id => _get(questions, id, id));
	}
	if (_has(survey, ['questionHashes', 'names'])) {
		const questionsName = Object.keys(
			_get(survey, ['questionHashes', 'names'], {})
		);
		nameAndIdInData = questionsName.reduce((accumulator, name) => {
			const questionId = _get(survey.getQuestionByName(name), 'questionId', '');
			if (data[questionId]) {
				return {
					...accumulator,
					[name]: data[questionId],
				};
			}
			return accumulator;
		}, {});
	}
	return { ...defaultData, ...nameAndIdInData, ...nameAndIdInTemplate };
};

export const getFilesToUpload = (files = []) =>
	produce(files, draft =>
		draft.reduce((acc, curr) => {
			const id = uuid();
			const size = _get(curr, 'size');
			const name = _get(curr, 'name');
			const newFile = {
				id,
				action: FILE_UPLOADER_ACTION_ADD,
				name,
				type: _get(curr, 'type'),
				size,
				file: curr,
				content: { id, size },
			};
			return [...acc, newFile];
		}, [])
	);

// eslint-disable-next-line no-param-reassign
export const resetQuestionValue = options => (options.question.value = []);

export const formatSurveyData = (survey, formTemplate) => {
	const data = switchNameToId(survey, formTemplate);
	return {
		...data,
	};
};

export const uploadFilesHandler = (
	files = [],
	urls = [],
	saveFormFiles = () => ''
) => {
	const fileUploads = files.reduce((result, { filesToUpload }) => {
		const uploadData = filesToUpload.map(item => {
			const uploadUrl = urls.find(urlItem => urlItem.id === item.id);

			return {
				file: _get(item, 'file', {}),
				url: _get(uploadUrl, 'url', ''),
				options: {
					headers: {
						'Content-Type': _get(item, 'type'),
					},
				},
			};
		});

		return [...result, ...uploadData];
	}, []);

	return saveFormFiles(fileUploads);
};

export const onFilesUploadCallbackHandler = (
	options = {},
	getFormFiles = () => ''
) => {
	const { files = [], question = {} } = options;
	const questionId = _get(question, 'questionId', '');
	const filesToUpload = getFilesToUpload(files);
	if ('callback' in options) options.callback('success', filesToUpload);
	return getFormFiles({ questionId, filesToUpload });
};

export const isFormCounterOffer = formType =>
	formType === FORM_TYPE.counterOffer;
