import { Grid } from '@material-ui/core';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import SubCard from '../../../../ui-component/cards/SubCard';
import { gridSpacing } from '../../../../store/constant';
import { TOffer, TFile } from '../../../../types';
import UploadFile from '../../components/UploadFile';
import { FILE_CONFIG } from '../config';
import config from '../../../../config';
import Contingency from './Contingency';
import OfferInformation from './OfferInformation';
import BuyerInformation from './BuyerInformation';
import SellerInformation from './SellerInformation';

type Props = {
	offer: TOffer;
	PAFiles: TFile[];
	onPAChange: (files: TFile[]) => void;
	SCARFiles: TFile[];
	onSCARChange: (files: TFile[]) => void;
};

const Details = ({
	offer,
	PAFiles,
	onPAChange,
	SCARFiles,
	onSCARChange,
}: Props) => {
	if (_isEmpty(offer)) return <></>;

	const currentStepId = +_get(offer, 'StepId', 0);
	const isShowPA = currentStepId >= config.offerSteps.paBeingCreated;
	const isEnablePA = currentStepId === config.offerSteps.paBeingCreated;
	const isShowSCAR = currentStepId >= config.offerSteps.reviewScarRequest;
	const isEnableSCAR = currentStepId === config.offerSteps.reviewScarRequest;

	return (
		<Grid container spacing={gridSpacing}>
			<Grid item lg={6} xs={12}>
				<Grid container direction='column'>
					<Grid item xs={12}>
						<Contingency offer={offer} />
					</Grid>
					<Grid item xs={12}>
						<OfferInformation offer={offer} />
					</Grid>
				</Grid>
			</Grid>
			<Grid item lg={6} xs={12}>
				<Grid container direction='column' spacing={gridSpacing}>
					<Grid item xs={12}>
						<BuyerInformation offer={offer} />
					</Grid>
					<Grid item xs={12}>
						<SellerInformation offer={offer} />
					</Grid>
					{isShowPA && (
						<Grid item xs={12}>
							<SubCard title='Signed Purchase Agreement'>
								<UploadFile
									name='PA'
									files={PAFiles}
									onDrop={onPAChange}
									accept={FILE_CONFIG.type}
									disabled={!isEnablePA}
								/>
							</SubCard>
						</Grid>
					)}
					{isShowSCAR && (
						<Grid item xs={12}>
							<SubCard title='Request for Repair'>
								<UploadFile
									name='SCAR'
									files={SCARFiles}
									onDrop={onSCARChange}
									accept={FILE_CONFIG.type}
									disabled={!isEnableSCAR}
								/>
							</SubCard>
						</Grid>
					)}
				</Grid>
			</Grid>
		</Grid>
	);
};

export default Details;
