import React, { useState } from 'react';
import _get from 'lodash.get';
import PhoneNumberInput from 'react-phone-input-2';
import { ELEMENTS_TYPE, SURVEYJS, ELEMENTS_TITLE } from '../../Constants.js';
import 'react-phone-input-2/lib/style.css';
import { convertPhoneValue } from '../../../../../../helpers';

const PhoneInputComponent = props => {
	const { question } = props;
	const [phoneValue, setValue] = useState(question.value);
	const onValueChangeHandler = value => {
		const formattedValue = convertPhoneValue(value);
		question.value = formattedValue;
		setValue(formattedValue);
	};
	return (
		<PhoneNumberInput
			disabled={question.isReadOnly}
			country='us'
			value={phoneValue}
			onChange={onValueChangeHandler}
			placeholder=''
		/>
	);
};

export const PhoneInput = Survey => {
	const widgetType = ELEMENTS_TYPE.phoneInput;
	const widget = {
		name: widgetType,
		title: ELEMENTS_TITLE.phoneInput,
		iconName: '',
		// eslint-disable-next-line react/display-name
		render: question => <PhoneInputComponent question={question} />,
		widgetIsLoaded: () => true,
		isFit: question => question.getType() === widgetType,
		activatedByChanged: () => {
			const metaData = _get(Survey, SURVEYJS.metaData);
			metaData.addClass(widgetType, [], null, 'empty');
		},
	};

	const Instance = _get(Survey, SURVEYJS.widgetCollectionInstance);
	Instance.addCustomWidget(widget, SURVEYJS.customType);
};
