import * as React from 'react';
import { Survey, Model } from 'survey-react';
import 'survey-react/modern.css';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import {
	RadioGroup,
	Checkbox,
	DatetimePicker,
	Dropdown,
	SingleInput,
	Textarea,
	MultipleText,
	MatrixSingle,
	MatrixMultiple,
	MatrixDynamic,
	Boolean,
	PhoneInput,
	Panel,
	Expression,
	FileUpload,
	PanelDynamic,
} from './Elements';
import './Styles.css';
import './Elements/Step/Style.css';
import './Elements/Step/Button.css';
import { onValidateHandler } from './Validators';
import { ELEMENTS_TYPE, SURVEYJS_MODE } from './Constants';
import './setting';
import {
	isFormInDisplayMode,
	applyFormProperty,
	switchIdToName,
} from '../../../../helpers/survey';

type Props = {
	formTemplate: any;
	onCompleteHandler: Function | undefined;
	onPageChangedHandler: Function | undefined;
	onFilesUploadHandler?: Function;
	formData: Object;
	mode: string;
	formProperty?: Object;
	onAfterRenderSurveyHandler?: Function;
};
const surveyModel = (template: string, mode: string) => {
	const survey = new Model(template);

	survey.completeText = 'Submit';

	survey.onUpdateQuestionCssClasses.add((survey, options) => {
		[
			RadioGroup,
			Checkbox,
			DatetimePicker,
			Dropdown,
			SingleInput,
			Textarea,
			MultipleText,
			MatrixSingle,
			MatrixMultiple,
			MatrixDynamic,
			Boolean,
			PhoneInput,
			Expression,
			FileUpload,
			PanelDynamic,
		].map(control => control(survey, options));
	});

	survey.onUpdatePanelCssClasses.add((senders, options) => {
		Panel(options);
	});

	survey.onAfterRenderQuestion.add((sender, options) => {
		const question = _get(options, 'question');
		if (question.getType() === ELEMENTS_TYPE.file) {
			question.storeDataAsText = false;
			question.allowImagesPreview = false;
		}
	});
	if (isFormInDisplayMode(mode)) survey.mode = SURVEYJS_MODE.display;

	return survey;
};

const Engine = (props: Props) => {
	const {
		formTemplate,
		onCompleteHandler,
		onPageChangedHandler,
		mode,
		formData,
		onFilesUploadHandler,
		formProperty,
		onAfterRenderSurveyHandler,
	} = props;

	const template = JSON.parse(formTemplate);
	const survey = surveyModel(template, mode);

	const removeSaveExitButton = () => {
		const saveExitButton = document.getElementById('save-exit-btn');
		if (saveExitButton) saveExitButton.remove();
	};

	const fillFormData = () => {
		survey.data = switchIdToName(survey, formData, template);
	};

	survey.onUploadFiles.add((survey, options) => {
		if (onFilesUploadHandler) {
			onFilesUploadHandler({ survey, options });
		}
	});

	survey.onComplete.add(survey => {
		if (!isFormInDisplayMode(mode) && onCompleteHandler)
			onCompleteHandler(survey, template);
	});

	survey.onCurrentPageChanged.add((survey, options) => {
		if (
			_get(options, 'isNextPage') &&
			!isFormInDisplayMode(mode) &&
			onPageChangedHandler
		) {
			onPageChangedHandler(survey, template);
		}
	});

	if (onAfterRenderSurveyHandler) {
		survey.onAfterRenderSurvey.add(() => {
			if (onAfterRenderSurveyHandler) onAfterRenderSurveyHandler();
		});
	}

	const { submitOnConfirmation, confirmationMessage = '' } = template;
	submitOnConfirmation &&
		confirmationMessage &&
		survey.onCompleting.add((sender, options) => {
			// eslint-disable-next-line no-param-reassign,no-alert
			options.allowComplete = window.confirm(confirmationMessage);
		});

	React.useEffect(() => {
		// filtering to get a list of type file questions
		survey.getAllQuestions().forEach(question => {
			applyFormProperty(question, formProperty);
		});
	}, []);

	React.useEffect(() => {
		if (isFormInDisplayMode(mode)) removeSaveExitButton();
	}, [mode]);

	React.useEffect(() => {
		if (_isEmpty(formData)) return;
		fillFormData();
	}, [formData]);

	return <Survey model={survey} onValidateQuestion={onValidateHandler} />;
};

export default Engine;
