export function getSortVariable<T>(
	headCell: HeadCell<T> | undefined,
	order: string
) {
	if (!headCell) return {};
	return {
		SortBy: [JSON.stringify({ [headCell.id]: order })],
	};
}

const HOME_SALE_CONTINGENCY = 'SaleOfHomeContingency';

export const displayContingency = (offer: TOffer, name: string) => {
	if (name === HOME_SALE_CONTINGENCY) {
		return _get(offer, `CounterOffer.BuyerCounter.${name}`) || '';
	}
	const contingency = _get(offer, `CounterOffer.BuyerCounter.${name}`);
	if (!contingency) return '';
	const duration = _get(offer, `CounterOffer.BuyerCounter.${name}Days`);
	return +duration ? `duration ${duration} days` : 'None';
}

const OFFER_FUNDING_TYPE = {
	cash: 'Cash',
	financing: 'Financing',
};

export const getProofOfFunds = (offer: TOffer): string => {
	const funding = _get(offer, 'Funding') || {};
	const {
		PurchaseType: type,
		CashProofOfFundsAmount: cashPof,
		FinancingProofOfFundsAmount: financingPof,
	} = funding;
	if (!type) return '';
	const pofValue = type === OFFER_FUNDING_TYPE.cash ? cashPof : financingPof
	return formatCurrency(pofValue || 0);
};
