import { FC, useMemo, useState } from 'react';
import { useQuery, gql, ApolloError, useMutation } from '@apollo/client';
import { Grid } from '@material-ui/core';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import config from '../../../config';
import { TOffer } from '../../../types';
import { updateOfferStep } from '../../../graphql/mutations';
import { listOfferSteps } from '../../../graphql/queries';
import Loader from '../../../ui-component/Loader';
import { useSnackBar } from '../../../hooks';
import { ActionButton, TAction } from './ActionButton';
import CounterOfferDialog from './components/CounterOfferDialog';
import { isDocumentExits } from '../../../helpers';
import { PA_FIELD, SCAR_FIELD } from './config';
import { ConfirmationDialog } from './components/ConfirmationDialog';

const { offerSteps } = config;

const advisorActions: number[] = [
	offerSteps.advisorNeedToReviewNewOffer,
	offerSteps.advisorNeedToReReviewSuggestion,
	offerSteps.advisorNeedToReviewOffer,
	offerSteps.awaitingAdvisorApproval,
	offerSteps.paBeingCreated,
	offerSteps.reviewScarRequest,
];

type Props = {
	offer: TOffer;
};

export const OfferActions: FC<Props> = ({ offer }: Props) => {
	const [openCounterOfferDialog, setOpenCounterOfferDialog] = useState(false);
	const { loading: loadingSteps, data } = useQuery(gql(listOfferSteps), {
		fetchPolicy: 'cache-first',
	});

	const steps = _get(data, 'listOfferSteps', []);
	const currentStepId: number = +_get(offer, 'StepId', 0);
	const [clickedAction, setClickedAction] = useState<TAction | undefined>(
		undefined
	);
	const [openConfirmDialog, setOpenConfirmDialog] = useState(false);

	const isOfferStep = (stepId: number) => currentStepId === stepId;

	const isMissingPADocument =
		isOfferStep(offerSteps.paBeingCreated) && !isDocumentExits(offer, PA_FIELD);

	const isMissingSCARDocument =
		isOfferStep(offerSteps.reviewScarRequest) &&
		!isDocumentExits(offer, SCAR_FIELD);

	// No action for missing document offer
	if (isMissingPADocument || isMissingSCARDocument) return <></>;

	// Only show advisor actions
	if (!advisorActions.includes(currentStepId)) return <></>;

	const offerId = _get(offer, 'OfferId');
	const currentStep = steps.find(
		({ id }: { id: number }) => id === currentStepId
	);

	const actions = useMemo(() => {
		const allowedActions = _get(currentStep, 'allowed_actions', []);
		if (allowedActions.length)
			return [...allowedActions]
				.filter(action => action.form_action !== true)
				.reverse();
		return allowedActions;
	}, [currentStep]);

	const anotherActions = useMemo(() => {
		const allowedActions = _get(currentStep, 'allowed_actions', []);
		if (allowedActions.length)
			return [...allowedActions]
				.filter(action => action.form_action === true)
				.reverse();
		return allowedActions;
	}, [currentStep]);

	// Update step mutation
	const showSnackBar = useSnackBar();
	const [updateStep, { loading }] = useMutation(gql(updateOfferStep), {
		onCompleted: (data: { updateOfferStep: TOffer | null }) => {
			if (_isEmpty(data) || _isEmpty(data.updateOfferStep)) return;
			window.location.reload();
		},
		onError: (error: ApolloError) => {
			showSnackBar({
				message: _get(error, 'message', 'Something went wrong'),
				variant: 'alert',
				alertSeverity: 'error',
			});
		},
	});

	const updateStepById = (nextStep: number) =>
		updateStep({
			variables: { OfferId: offerId, NewStepId: nextStep },
		});

	const handleClickAction = (action: TAction) => () => {
		setClickedAction(action);
		setOpenConfirmDialog(true);
	};

	const handleActionConfirm = () => {
		if (clickedAction) updateStepById(clickedAction.next_step_id);
		setOpenConfirmDialog(false);
	};

	return (
		<>
			{(loading || loadingSteps) && <Loader />}
			<Grid container spacing={2}>
				{actions.map((action: TAction) => (
					<ActionButton
						key={`${offerId}-${action.next_step_id}`}
						action={action}
						onClick={handleClickAction(action)}
						disabled={loading}
					/>
				))}
				<ConfirmationDialog
					open={openConfirmDialog}
					action={clickedAction}
					onClose={() => setOpenConfirmDialog(false)}
					onConfirm={handleActionConfirm}
				/>
				{anotherActions.map((action: TAction) => (
					<div className='MuiGrid-item' key={`${offerId}-${action.next_step_id}`}>
						<CounterOfferDialog
							open={openCounterOfferDialog}
							offer={offer}
							handleClickCloseDialog={() => {
								setOpenCounterOfferDialog(false);
							}}
						/>
						<ActionButton
							action={action}
							onClick={() => setOpenCounterOfferDialog(true)}
							disabled={loading}
						/>
					</div>
				))}
			</Grid>
		</>
	);
};

export default OfferActions;
