import _isObject from 'lodash.isobject';
import { ELEMENTS_TYPE, SURVEYJS_COMMON_CSS_CLASSES } from '../../Constants';

export const PanelDynamic = (survey, options) => {
	const { question, cssClasses = {} } = options;

	if (_isObject(question) && question.getType() === ELEMENTS_TYPE.panelDynamic) {
		cssClasses.mainRoot = SURVEYJS_COMMON_CSS_CLASSES.mainRoot;
		cssClasses.root += ' sv-paneldynamic-root';
		cssClasses.button += ' sv-paneldynamic-btn';
		cssClasses.buttonAdd += ' sv-btn-primary';
	}
};
