import { Button, Dialog, DialogContent, Box } from '@material-ui/core';
import _get from 'lodash.get';
import { TAction } from '../ActionButton';

type Props = {
	action: TAction | undefined;
	open: boolean;
	onClose: () => void;
	onConfirm: () => void;
};

export const ConfirmationDialog = ({
	open,
	action,
	onClose,
	onConfirm,
}: Props) => {
	const message = _get(action, 'action', '');

	return (
		<Dialog open={open} onClose={onClose}>
			<DialogContent sx={{ px: 3, py: 2, minWidth: { md: 600 } }}>
				<Box sx={{ mb: 2 }}>
					Are you sure? Do you want to {message.toLowerCase()}?
				</Box>
				<Box sx={{ display: 'flex', justifyContent: 'flex-end' }}>
					<Button
						onClick={onClose}
						variant='text'
						sx={{ color: 'error.main', mr: 1 }}
					>
						No
					</Button>
					<Button onClick={onConfirm} variant='contained' color='primary'>
						Yes
					</Button>
				</Box>
			</DialogContent>
		</Dialog>
	);
};
