import _isObject from 'lodash.isobject';
import './Styles.css';
import { ELEMENTS_TYPE, SURVEYJS_COMMON_CSS_CLASSES } from '../../Constants';

const SingleInput = (survey, options) => {
	const TYPE = ELEMENTS_TYPE.singleInput;
	const { question, cssClasses = {} } = options;
	if (_isObject(question) && question.getType() === TYPE) {
		cssClasses.mainRoot = SURVEYJS_COMMON_CSS_CLASSES.mainRoot;
		cssClasses.title = SURVEYJS_COMMON_CSS_CLASSES.title;
		cssClasses.root = 'text_inputer form2';
		cssClasses.content = '';
		if (question.isReadOnly) cssClasses.root += ' read-only';
	}
};

export { SingleInput };
