import _get from 'lodash.get';
import _isObject from 'lodash.isobject';
import {
	ELEMENTS_TYPE,
	SURVEYJS_PROPERTY,
	SURVEYJS_COMMON_CSS_CLASSES,
} from '../../Constants';
import './Styles.css';

export const Expression = (survey, options) => {
	const TYPE = ELEMENTS_TYPE.expression;
	const question = _get(options, 'question');

	if (_isObject(question) && question.getType() === TYPE) {
		const cssType = _get(question, SURVEYJS_PROPERTY.expressionCssType);
		const cssClasses = _get(options, 'cssClasses');

		cssClasses.title = SURVEYJS_COMMON_CSS_CLASSES.title;
		cssClasses.mainRoot +=
			` sv-expression-custom-wrapper ${  SURVEYJS_COMMON_CSS_CLASSES.mainRoot}`;

		if (cssType === 'colorfilled') {
			const colorfilledCssType = ' color-filled';
			cssClasses.title += colorfilledCssType;
			cssClasses.content += colorfilledCssType;
		}
	}
};
