import { useEffect, useState } from 'react';
import { useQuery, gql, useMutation, ApolloError } from '@apollo/client';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import { LinearProgress } from '@material-ui/core';
import graphqlSchema from '../../../../graphql';
import { useSnackBar } from '../../../../hooks';
import { getFormMode, formatSurveyData } from '../../../../helpers/survey';
import Engine from '../../components/FormBuilder/Engine';

type Props = {
	offerId: string;
	formId: string;
	handleClickCloseDialog: Function;
	onAfterRenderSurveyHandler: Function;
};

const FormModalContent = (props: Props) => {
	const showSnackBar = useSnackBar();
	const [isGoingToExit, setExitStatus] = useState(false);
	const { formId, offerId, handleClickCloseDialog, onAfterRenderSurveyHandler } =
		props;

	const { data: formData, loading } = useQuery(
		gql(graphqlSchema.getSellerCounterOfferFormData),
		{
			variables: {
				CounterOfferId: offerId,
				FormId: formId,
			},
		}
	);

	const [doSubmit] = useMutation(
		gql(graphqlSchema.updateSellerCounterOfferFormData),
		{
			onCompleted: () => {
				showSnackBar({
					message: 'Counter Offer submitted',
					alertSeverity: 'success',
				});
				window.location.reload();
			},
			onError: (error: ApolloError) => {
				showSnackBar({
					message: _get(error, 'message', 'Something went wrong'),
					alertSeverity: 'error',
				});
			},
		}
	);

	const template = _get(
		formData,
		['formdata_GetSellerCounterOffer', 'Form', 'Template'],
		{}
	);
	const formSavedData = _get(
		formData,
		['formdata_GetSellerCounterOffer', 'FormData'],
		{}
	);
	const dataValue = _get(formSavedData, 'DataValue', '');
	const formProperty = _get(
		formData,
		['formdata_GetSellerCounterOffer', 'FormProperty'],
		{}
	);
	const mode = getFormMode(_get(formSavedData, ['FormProperty'], ''));

	const saveData = (survey: any, formTemplate: string) => {
		try {
			doSubmit({
				variables: {
					CounterOfferId: offerId,
					FormId: formId,
					FormData: JSON.stringify(formatSurveyData(survey, formTemplate)),
				},
			});
		} catch (e) {
			showSnackBar({
				message: _get(e, 'message', 'Something went wrong'),
				alertSeverity: 'error',
			});
		}
	};

	const onCompleteHandler = (survey: any, formTemplate: string) => {
		saveData(survey, formTemplate);
		survey.clear(false);
		setExitStatus(true);
	};

	const onPageChangedHandler = (survey: any, formTemplate: string) => {
		saveData(survey, formTemplate);
	};

	useEffect(() => {
		if (isGoingToExit) {
			handleClickCloseDialog();
		}
	}, [isGoingToExit]);

	if (loading) return <LinearProgress />;

	return (
		<div className='container'>
			{!_isEmpty(template) && (
				<Engine
					mode={mode}
					formTemplate={template}
					onCompleteHandler={onCompleteHandler}
					onPageChangedHandler={onPageChangedHandler}
					formData={_isEmpty(dataValue) ? {} : JSON.parse(dataValue)}
					onAfterRenderSurveyHandler={onAfterRenderSurveyHandler}
					formProperty={_isEmpty(formProperty) ? {} : JSON.parse(formProperty)}
				/>
			)}
		</div>
	);
};

export default FormModalContent;
