import { List } from '@material-ui/core';
import _get from 'lodash.get';
import { TOffer } from '../../../../types';
import SubCard from '../../../../ui-component/cards/SubCard';
import TextItem from '../../components/TextItem';
import {
	displayFurniture,
	formatCurrency,
	getOfferPrice,
	displayRentBack,
	getCounterData,
} from '../../../../helpers';
import { displayContingency, getProofOfFunds } from '../helper';

type Props = {
	offer: TOffer;
};

const OfferInformation = ({ offer }: Props) => (
	<SubCard title='Offer Information'>
		<List>
			<TextItem<TOffer> data={offer} source='OfferNo' label='Offer No' />
			<TextItem<TOffer>
				data={offer}
				getValue={getOfferPrice}
				label='Offer Price'
			/>
			<TextItem<TOffer>
				data={offer}
				source='Funding.PurchaseType'
				label='Funding Type'
				isHiddenEmptyValue
			/>
			<TextItem<TOffer>
				data={offer}
				source='FirmInfo.LoanType'
				label='Loan Type'
			/>
			<TextItem<TOffer>
				data={offer}
				getValue={row => formatCurrency(_get(row, 'Funding.DownPayment'))}
				label='Down Payment'
				isHiddenEmptyValue
			/>
			<TextItem<TOffer>
				data={offer}
				getValue={getProofOfFunds}
				label='Proof of Funds'
				isHiddenEmptyValue
			/>
			<TextItem<TOffer>
				data={offer}
				source='CounterOffer.BuyerCounter.FinanceContingency'
				label='Finance Contingency'
				getValue={row => displayContingency(row, 'FinanceContingency')}
				isHiddenEmptyValue
			/>
			<TextItem<TOffer>
				data={offer}
				source='CounterOffer.BuyerCounter.InspectionContingency'
				label='Inspection Contingency'
				getValue={row => displayContingency(row, 'InspectionContingency')}
				isHiddenEmptyValue
			/>
			<TextItem<TOffer>
				data={offer}
				source='CounterOffer.BuyerCounter.AppraisalContingency'
				label='Appraisal Contingency'
				getValue={row => displayContingency(row, 'AppraisalContingency')}
				isHiddenEmptyValue
			/>
			<TextItem<TOffer>
				data={offer}
				source='CounterOffer.BuyerCounter.SaleOfHomeContingency'
				label='Home Sale Contingency'
				getValue={row => displayContingency(row, 'SaleOfHomeContingency')}
				isHiddenEmptyValue
			/>
			<TextItem<TOffer>
				data={offer}
				source='CounterOffer.BuyerCounter.ClosingPeriod'
				label='Closing Period'
				isHiddenEmptyValue
			/>
			<TextItem<TOffer>
				data={offer}
				getValue={displayFurniture}
				label='Personal Item Requests'
			/>
			<TextItem<TOffer>
				data={offer}
				getValue={row => displayRentBack(getCounterData(row))}
				label='Rent Back'
				isHiddenEmptyValue
			/>
			<TextItem<TOffer>
				data={offer}
				source='PropertyData.FullAddress'
				label='Property Address'
			/>
			<TextItem<TOffer>
				data={offer}
				source='PropertyData.OwnerNames'
				label='Sellers'
			/>
		</List>
	</SubCard>
);

export default OfferInformation;
