import _isEmpty from 'lodash.isempty';
import { makeStyles } from '@material-ui/core/styles';
import {
	TableContainer,
	Table,
	TableBody,
	TableRow,
	TableCell,
} from '@material-ui/core';
import SubCard from '../../../../../ui-component/cards/SubCard';
import { TOffer } from '../../../../../types';
import { getHistoryRows } from './helpers';

type Props = {
	offer: TOffer;
};

const useStyles = makeStyles(theme => ({
	table: {
		minWidth: 750,
		tableLayout: 'fixed',

		'& tr:first-child': {
			textTransform: 'capitalize',
			'& td': {
				fontWeight: 'bold',
			},
		},
	},
	cell: {
		whiteSpace: 'break-spaces',
		border: `1px solid ${theme.palette.grey[300]}`,
	},
	row: {
		'& td:first-child': {
			textTransform: 'capitalize',
			fontWeight: 'bold',
			backgroundColor: theme.palette.grey[50],
		},
		'& td:nth-child(2)': {
			backgroundColor: theme.palette.grey[50],
		},
	},
}));

const History = ({ offer }: Props) => {
	const classes = useStyles();
	if (_isEmpty(offer)) return <></>;

	const rows = getHistoryRows(offer);

	return (
		<SubCard>
			<TableContainer>
				<Table className={classes.table}>
					<TableBody>
						{rows.map((row, rowIndex) => {
							const rowKey = `history-row-${rowIndex}`;
							return (
								<TableRow className={classes.row} key={rowKey}>
									{row.map((cell, cellIndex) => {
										const cellKey = `${rowKey}-cell-${cellIndex}`;
										return (
											<TableCell className={classes.cell} key={cellKey}>
												{cell}
											</TableCell>
										);
									})}
								</TableRow>
							);
						})}
					</TableBody>
				</Table>
			</TableContainer>
		</SubCard>
	);
};

export default History;
